let oldConsole: any = undefined
export function disableConsole() {
	oldConsole = console.log
	console.log = function() {}
}
export function enableConsole() {
	if (oldConsole) {
		console.log = oldConsole
	}
}
export function hasConstructor<T>(instance: any, cons: { new (...args: any[]): T }): instance is T {
	return instance.constructor == cons
}

export function getIntervals(ts: number[], min: number, max: number): [number, number][] {
	ts.sort((a, b) => a - b)
	if (!eq(ts[0], min)) {
		ts.splice(0, 0, min)
	}
	if (!eq(ts.last, max)) {
		ts.push(max)
	}
	return arrayFromFunction(ts.length - 1, i => [ts[i], ts[i + 1]] as [number, number])
}

export function assertVectors(...vectors: (Vector | V3)[]) {
	if (NLA_DEBUG) {
		for (let i = 0; i < arguments.length; i++) {
			if (!(arguments[i] instanceof V3 || arguments[i] instanceof Vector)) {
				throw new Error(
					'assertVectors arguments[' +
						i +
						'] is not a vector. ' +
						typeof arguments[i] +
						' == typeof ' +
						arguments[i],
				)
			}
		}
	}
	return true
}
export function assertInst<T>(what: Function & { prototype: T }, ...objs: T[]) {
	if (NLA_DEBUG) {
		for (let i = 0; i < objs.length; i++) {
			if (!(objs[i] instanceof what)) {
				throw new Error(
					'assertInst objs[' +
						i +
						'] is not a ' +
						what.prototype.name +
						'. ' +
						objs[i].constructor.name +
						objs[i],
				)
			}
		}
	}
	return true
}
export function assertNumbers(...numbers: number[]) {
	if (NLA_DEBUG) {
		for (let i = 0; i < numbers.length; i++) {
			if ('number' !== typeof numbers[i]) {
				throw new Error(
					`assertNumbers arguments[${i}] is not a number. ${typeof numbers[i]} == typeof ${numbers[i]}`,
				)
			}
		}
	}
	return true
}
export function assertInts(...numbers: number[]) {
	if (NLA_DEBUG) {
		for (let i = 0; i < numbers.length; i++) {
			if ('number' !== typeof numbers[i] || numbers[i] % 1 !== 0) {
				throw new Error(
					`assertNumbers arguments[${i}] is not an int. ${typeof numbers[i]} == typeof ${numbers[i]}`,
				)
			}
		}
	}
	return true
}
export function assert(value: any, ...messages: (any | (() => string))[]): value is true {
	if (NLA_DEBUG && !value) {
		throw new Error(
			'assert failed: ' +
				messages.map(message => ('function' === typeof message ? message() : message || '')).join('\n'),
		)
	}
	return true
}
export function assertNever(value?: never): never {
	throw new Error()
}
export function assertf(f: () => any, ...messages: (any | (() => any))[]) {
	if (NLA_DEBUG && !f()) {
		throw new Error(
			'assertf failed: ' +
				f.toString() +
				messages.map(message => ('function' === typeof message ? message() : message || '')).join('\n'),
		)
	}
}

export function lerp(a: number, b: number, t: number) {
	return a * (1 - t) + b * t
}
export function lerpInv(a: number, b: number, t: number) {
	return (t - a) / (b - a)
}
const originalNumberToString = Number.prototype.toString
Number.prototype.toString = function(radix) {
	if (PI == this) {
		return 'PI'
	}
	return originalNumberToString.call(this, radix)
}

export const eq0 = (x: number, EPS: number = NLA_PRECISION): boolean => Math.abs(x) <= EPS
export const eq = (x: number, y: number, EPS: number = NLA_PRECISION): boolean => Math.abs(x - y) <= EPS
export const lt = (x: number, y: number, EPS: number = NLA_PRECISION): boolean => x - y < -EPS
export const gt = (x: number, y: number, EPS: number = NLA_PRECISION): boolean => y - x < -EPS
export const le = (x: number, y: number, EPS: number = NLA_PRECISION): boolean => x - y <= EPS
export const ge = (x: number, y: number, EPS: number = NLA_PRECISION): boolean => y - x <= EPS

export const eqAngle = (x: number, y: number): boolean => zeroAngle(x - y)
export const zeroAngle = (x: number): boolean =>
	((x % (2 * Math.PI)) + 2 * Math.PI + NLA_PRECISION) % (2 * Math.PI) < 2 * NLA_PRECISION
export const snap = (x: number, to: number): number => (Math.abs(x - to) <= NLA_PRECISION ? to : x)
export const snap2 = (x: number, ...to: number[]): number =>
	to.reduce((x, to) => (Math.abs(x - to) <= NLA_PRECISION ? to : x), x)
export const snapEPS = (x: number, EPS: number, ...to: number[]): number =>
	to.reduce((x, to) => (Math.abs(x - to) <= EPS ? to : x), x)
export const snap0 = (x: number, EPS = NLA_PRECISION): number => (Math.abs(x) <= EPS ? 0 : x)
export const canonAngle = (x: number): number => ((x % (2 * Math.PI)) + 2 * Math.PI) % (2 * Math.PI)

/** @deprecated */ export const eq02 = eq0
/** @deprecated */ export const eq2 = eq

/**
 * Decimal adjustment of a number.
 *
 * @param f  The type of adjustment.
 * @param value The number.
 * @param exp The exponent (the 10 logarithm of the adjustment base).
 * @returns The adjusted value.
 */
function decimalAdjust(f: (x: number) => number, value: number, exp: number): number {
	// If the exp is undefined or zero...
	if (typeof exp === 'undefined' || +exp === 0) {
		return f(value)
	}
	value = +value
	exp = +exp
	// If the value is not a number or the exp is not an integer...
	if (isNaN(value) || !(typeof exp === 'number' && exp % 1 === 0)) {
		return NaN
	}
	// Shift
	let vs = value.toString().split('e')
	value = f(+(vs[0] + 'e' + (vs[1] ? +vs[1] - exp : -exp)))
	// Shift back
	vs = value.toString().split('e')
	return +(vs[0] + 'e' + (vs[1] ? +vs[1] + exp : exp))
}

export const round10: (value: number, exp: number) => number = decimalAdjust.bind(undefined, Math.round)
export const floor10: (value: number, exp: number) => number = decimalAdjust.bind(undefined, Math.floor)
export const ceil10: (value: number, exp: number) => number = decimalAdjust.bind(undefined, Math.ceil)

export const GOLDEN_RATIO = (1 + Math.sqrt(5)) / 2

export function mod(a: number, b: number) {
	return ((a % b) + b) % b
}
export function arraySwap(arr: any[], i: int, j: int) {
	const temp = arr[i]
	arr[i] = arr[j]
	arr[j] = temp
}
export type ArrayLikeWriteAble<T> = {
	readonly length: int
	[index: number]: T
}
export function arrayCopy<T, A extends ArrayLikeWriteAble<T>>(
	src: ArrayLike<T>,
	sstart: int,
	dst: A,
	dstart: int,
	length: int,
): A {
	assertInts(sstart, dstart, length)
	dstart += length
	length += sstart
	while (length-- > sstart) {
		dst[--dstart] = src[length]
	}
	return dst
}
export function clamp(val: number, min: number, max: number) {
	assertNumbers(val, min, max)
	return Math.max(min, Math.min(max, val))
}

export function between(val: number, min: number, max: number) {
	assertNumbers(val, min, max)
	return min <= val && val <= max
}
export function fuzzyBetween(val: number, min: number, max: number) {
	return le(min, val) && le(val, max)
}

export function mapPush<T, U>(map: Map<T, U[]>, key: T, val: U) {
	const array = map.get(key)
	if (array) {
		array.push(val)
	} else {
		map.set(key, [val])
	}
}

/**
 * Copies a number of items from one array to another, with a definable step size between items in the source and
 * destination array.
 *
 * @param src The source array.
 * @param sstart The location of the first item in the source array.
 * @param sstep The offset between items in the source array.
 * @param dst The destination array.
 * @param dstart The location of the first item in the destination array.
 * @param dstep The offset between items in the destination array.
 * @param count The number of items to copy.
 */
export function arrayCopyStep<T>(
	src: ArrayLike<T>,
	sstart: int,
	sstep: int,
	dst: ArrayLikeWriteAble<T>,
	dstart: int,
	dstep: int,
	count: int,
): void {
	let srcIndex = sstart + count * sstep
	let dIndex = dstart + count * dstep
	while (srcIndex > sstart) {
		dst[(dIndex -= dstep)] = src[(srcIndex -= sstep)]
	}
}

/**
 * Copies a number of contiguous, evenly-spaced blocks from one array to another.
 *
 * @param src The source array.
 * @param sstart The start of the first block in the source array.
 * @param sstep The offset from the start of one block to the start of the next block in the source array.
 * @param dst The destination array.
 * @param dstart The start of the first block in the destination array.
 * @param dstep The offset from the start of one block to the start of the next block in the destination array.
 * @param blockSize The length of one block.
 * @param blockCount The number of blocks to copy.
 */
export function arrayCopyBlocks<T>(
	src: ArrayLike<T>,
	sstart: int,
	sstep: int,
	dst: ArrayLikeWriteAble<T>,
	dstart: int,
	dstep: int,
	blockSize: int,
	blockCount: int,
): void {
	for (let i = 0; i < blockCount; i++) {
		arrayCopy(src, sstart + sstep * i, dst, dstart + dstep * i, blockSize)
	}
}
export function arrayRange(startInclusive: int, endExclusive: int, step: int = 1) {
	assertNumbers(startInclusive, step)
	//console.log(Math.ceil((endExclusive - startInclusive) / step))
	const arrLength = Math.ceil((endExclusive - startInclusive) / step)
	const result = new Array(arrLength) // '- startInclusive' so that chunk in the last row will also be selected, even
	// if the row is not complete
	for (let i = startInclusive, index = 0; index < arrLength; i += step, index++) {
		result[index] = i
	}
	return result
}

/**
 * Returns a number of evenly-spaced values between t0 and t1 (inclusive).
 * @param t0 First value.
 * @param t1 Last value.
 * @param count Total number of values.
 */
export function arraySamples(t0: number, t1: number, count: int = 64) {
	return arrayFromFunction(count, i => lerp(t0, t1, i / (count - 1)))
}

export function arrayFromFunction<T>(length: 1, f: (i: int, total: int) => T): [T]
export function arrayFromFunction<T>(length: 2, f: (i: int, total: int) => T): [T, T]
export function arrayFromFunction<T>(length: 3, f: (i: int, total: int) => T): [T, T, T]
export function arrayFromFunction<T>(length: 4, f: (i: int, total: int) => T): [T, T, T, T]
export function arrayFromFunction<T>(length: 5, f: (i: int, total: int) => T): [T, T, T, T, T]
export function arrayFromFunction<T>(length: int, f: (i: int, total: int) => T): T[]
export function arrayFromFunction<T>(length: int, f: (i: int, total: int) => T): T[] {
	assertNumbers(length)
	assert('function' == typeof f)
	const a = new Array(length)
	let elIndex = length
	while (elIndex--) {
		a[elIndex] = f(elIndex, length)
	}
	return a
}

export function fuzzyUniques(vals: number[]): number[] {
	const round = (val: number) => Math.floor(val * (1 << 26)) / (1 << 26)
	const map = new Map()
	for (let i = 0; i < vals.length; i++) {
		const val = vals[i],
			roundVal = round(val)
		let key
		if (
			!map.has(roundVal) &&
			!((key = map.get(roundVal - 1 / (1 << 26))) && eq(key, val)) &&
			!((key = map.get(roundVal + 1 / (1 << 26))) && eq(key, val))
		) {
			map.set(roundVal, val)
		}
	}
	return Array.from(map.values())
}

export function fuzzyUniquesF<T>(vals: T[], f: (o: T) => number): T[] {
	const round = (val: number) => Math.floor(val * (1 << 26)) / (1 << 26)
	const map = new Map()
	for (let i = 0; i < vals.length; i++) {
		const val = vals[i],
			roundVal = round(f(val))
		let key
		if (
			!map.has(roundVal) &&
			!((key = map.get(roundVal - 1 / (1 << 26))) && eq(key, f(val))) &&
			!((key = map.get(roundVal + 1 / (1 << 26))) && eq(key, f(val)))
		) {
			map.set(roundVal, val)
		}
	}
	return Array.from(map.values())
}

export function addOwnProperties(target: any, props: any, ...exclude: string[]) {
	Object.getOwnPropertyNames(props).forEach(key => {
		//console.log(props, key)
		if (!exclude.includes(key)) {
			if (target.hasOwnProperty(key)) {
				console.warn('target ', target, ' already has property ', key, target[key])
			}
			Object.defineProperty(target, key, Object.getOwnPropertyDescriptor(props, key)!)
		}
	})
}

export let defaultRoundFunction = (x: number) => x // Math.round10(x, -4)

export function forceFinite(val: string): number {
	const valNum = parseFloat(val.replace(',', '.').replace(/^[^0-9,\.\-]/, ''))
	return Number.isFinite(valNum) ? valNum : 0
}

export const MINUS = (a: number, b: number) => a - b

export function floatHashCode(f: number) {
	return ~~(f * (1 << 28))
}

/**
 * combinations(2) will generate
 * [0,0] [0,1] [1,1] [0,2] [1,2] [2,2]
 */
export function* combinations(n: int): IterableIterator<{ i: number; j: number }> {
	for (let i = 0; i < n; i++) {
		for (let j = i; j < n; j++) {
			yield { i: i, j: j }
		}
	}
}

/* The arithmetic-geometric mean of two non-negative numbers */
export function arithmeticGeometricMean(x: number, y: number): number {
	assertf(() => lt(0, x))
	assertf(() => lt(0, y))
	let a = x,
		g = y
	let i = 30
	while (i-- && a != g) {
		;[a, g] = [(a + g) / 2, Math.sqrt(a * g)]
	}
	assert(i != -1)

	return a
}

/**
 * incomplete elliptic integral of the first kind
 * EllipticF(phi, k2) = INT[0; phi] 1 / sqrt(1 - k2 * sin(phi)) dphi
 */
export function EllipticF(phi: number, k2: number): number {
	return gaussLegendreQuadrature24(phi => Math.pow(1 - k2 * Math.sin(phi) ** 2, -0.5), 0, phi)
}

/**
 * incomplete elliptic integral of the second kind
 * EllipticE(phi, k2) = INT[0; phi] sqrt(1 - k2 * sin(phi)) dphi
 */
export function EllipticE(phi: number, k2: number): number {
	return gaussLegendreQuadrature24(phi => Math.pow(1 - k2 * Math.sin(phi) ** 2, 0.5), 0, phi)
}

/**
 * One degree in radians. Use like Math.sin(30 * DEG).
 */
export const DEG = 0.017453292519943295
export function rad2deg(rad: number): number {
	//  discuss at: http://phpjs.org/functions/deg2rad/
	// original by: Enrique Gonzalez
	// improved by: Thomas Grainger (http://graingert.co.uk)
	//   example 1: deg2rad(45)
	//   returns 1: 0.7853981633974483

	return rad / DEG
}

/**
 * numberToStr(2/3) == '0.6p'
 * numberToStr(7/12) == '0.583p'
 * numberToStr(2/7) == '0.285714pppppp'
 * numberToStr(NLA_PRECISION) == '0+'
 * numberToStr(-NLA_PRECISION) == '0-'
 * numberToStr(2-NLA_PRECISION) == '2-'
 * numberToStr(0) == '0='
 *
 */
export function numberToStr(value: number, length: int) {
	let minAbsDiff = Infinity,
		closestValue: number | undefined = undefined,
		closestValueStr: string | undefined = undefined
	function test(testValue: number, testValueStr: string) {
		const absDiff = Math.abs(testValue - value)
		console.log(testValue, testValueStr, absDiff)
		if (absDiff < minAbsDiff) {
			minAbsDiff = absDiff
			closestValue = testValue
			closestValueStr = testValueStr
		}
		return 0 == absDiff
	}

	function overline(str: string) {
		return str
			.split('')
			.map(c => c + '\u0304')
			.join('')
	}

	if (test(parseFloat(value.toFixed(length)), value.toFixed(length))) return closestValueStr + '='

	const valueStr = '' + value
	const toDecimal = valueStr.substr(0, valueStr.indexOf('.') + 1)
	const decimals = valueStr.substr(valueStr.indexOf('.') + 1)

	for (let startPos = 0; startPos < length; startPos++) {
		for (let endPos = startPos + 1; endPos <= length; endPos++) {
			const prefixDecimals = decimals.substr(0, startPos)
			const period = decimals.substr(startPos, endPos)
			const testValue = parseFloat(
				toDecimal + prefixDecimals + period.repeat(Math.ceil((17 - startPos) / period.length)),
			)
			if (test(testValue, toDecimal + prefixDecimals + overline(period))) return closestValueStr + '='
		}
	}

	return closestValueStr + (closestValue! < value ? '-' : '+')
}

export function time(f: () => void) {
	const start = performance.now()
	f()
	return performance.now() - start
}

export type Tuple2<T> = [T, T]
export type Tuple3<T> = [T, T, T]
export type Tuple4<T> = [T, T, T, T]
export type Tuple5<T> = [T, T, T, T, T]
export type Tuple6<T> = [T, T, T, T, T, T]
export type Tuple7<T> = [T, T, T, T, T, T, T]
export type Tuple8<T> = [T, T, T, T, T, T, T, T]
