export function isCCW(vertices: V3[], normal: V3) {
	const dsa = doubleSignedArea(vertices, normal)
	assert(0 != dsa)
	return dsa < 0
}
export function doubleSignedArea(vertices: V3[], normal: V3) {
	assert(!normal.likeO(), '!normal.likeO()')
	const absMaxDim = normal.maxAbsDim()
	// order is important, coord0 and coord1 must be set so that coord0, coord1 and maxDim span a right-hand coordinate
	// system var [coord0, coord1] = [['y', 'z'], ['z', 'x'], ['x', 'y']][maxAbsDim]
	const doubleSignedArea = vertices
		.map((v0, i, vertices) => {
			const v1 = vertices[(i + 1) % vertices.length]
			//return (v1[coord0] - v0[coord0]) * (v1[coord1] + v0[coord1])
			switch (absMaxDim) {
				case 0:
					return (v1.y - v0.y) * (v1.z + v0.z)
				case 1:
					return (v1.z - v0.z) * (v1.x + v0.x)
				case 2:
					return (v1.x - v0.x) * (v1.y + v0.y)
			}
		})
		.reduce((a, b) => a + b)
	return snap(doubleSignedArea * Math.sign(normal.e(absMaxDim)), 0)
}

/**
 * solves x + px + q = 0
 */
export function pqFormula(p: number, q: number): number[] {
	// 4 times the discriminant:in
	const discriminantX4 = (p * p) / 4 - q
	if (discriminantX4 < -NLA_PRECISION) {
		return []
	} else if (discriminantX4 <= NLA_PRECISION) {
		return [-p / 2]
	} else {
		const root = Math.sqrt(discriminantX4)
		return [-p / 2 - root, -p / 2 + root]
	}
}

/**
 * from pomax' library
 * solves ax + bx + cx + d = 0
 * This function from pomax' utils
 * @returns 0-3 roots
 */
export function solveCubicReal2(a: number, b: number, c: number, d: number): number[] {
	if (eq0(a)) {
		if (eq0(b)) {
			return [-d / c]
		} else {
			return pqFormula(c / b, d / b)
		}
	}
	const divisor = a
	a = b / divisor
	b = c / divisor
	c = d / divisor
	const p = (3 * b - a * a) / 3,
		pDiv3 = p / 3,
		pDiv3Pow3 = pDiv3 * pDiv3 * pDiv3,
		q = (2 * a * a * a - 9 * a * b + 27 * c) / 27,
		qDiv2 = q / 2,
		discriminant = qDiv2 * qDiv2 + pDiv3Pow3
	// 18abcd - 4bd + bc - 4ac - 27ad
	if (discriminant < -NLA_PRECISION / 8) {
		const r = Math.sqrt(-pDiv3Pow3),
			t = -q / (2 * r),
			cosphi = t < -1 ? -1 : t > 1 ? 1 : t, // clamp t to [-1;1]
			phi = Math.acos(cosphi),
			t1 = 2 * Math.cbrt(r)
		const x1 = t1 * Math.cos(phi / 3) - a / 3
		const x2 = t1 * Math.cos((phi + 2 * Math.PI) / 3) - a / 3
		const x3 = t1 * Math.cos((phi + 4 * Math.PI) / 3) - a / 3
		return [x1, x2, x3]
	} else if (discriminant <= NLA_PRECISION / 8) {
		if (0 == qDiv2) {
			// TODO: compare with likeO?
			return [-a / 3]
		}
		const u1 = qDiv2 < 0 ? Math.cbrt(-qDiv2) : -Math.cbrt(qDiv2)
		const x1 = 2 * u1 - a / 3
		const x2 = -u1 - a / 3
		return [x1, x2]
	} else {
		const sd = Math.sqrt(discriminant)
		const u1 = Math.cbrt(-qDiv2 + sd)
		const v1 = Math.cbrt(qDiv2 + sd)
		return [u1 - v1 - a / 3]
	}
}
export function checkDerivate(
	f: (x: number) => number,
	df: (x: number) => number,
	a: number,
	b: number,
	maxFaults: int = 0,
) {
	const eps = 1e-4
	let faults = 0
	for (let t = a; t < b; t += (b - a) / 100) {
		const dfdt = df(t)
		const df2 = (f(t + eps) - f(t)) / eps
		assert((faults += +!eq2(df2, dfdt, 0.1)) <= maxFaults, `df2 == ${df2} != ${df(t)} = df(t)`)
	}
}
export function getRoots(
	f: (x: number) => number,
	a: number,
	b: number,
	stepSize: number,
	df: (x: number) => number,
): number[] {
	const results: number[] = []
	for (let startT = a; startT <= b; startT += stepSize) {
		const dt = stepSize * abs(df(startT))
		if (abs(f(startT)) <= dt) {
			//const t = newtonIterate1d(f, startT, 16)
			let t = newtonIterateWithDerivative(f, startT, 16, df)
			if (!eq0(f(t)) || eq0(df(t))) {
				t = newtonIterate1d(df, startT, 16)
				// const a = startT - dt,
				// 	b = startT + dt
				//if (f(a) * f(b) < 0) {
				//    t = bisect(f, a, b, 16)
				//} else if (df(a) * df(b) < 0) {
				//    t = bisect(df, a, b, 16)
				//}
			}
			if (eq0(f(t)) && !results.some(r => eq(r, t))) {
				results.push(t)
			}
		}
	}
	return results
}

export function bisect(f: (x: number) => number, a: number, b: number, steps: int) {
	assert(a < b)
	let fA = f(a),
		fB = f(b)
	assert(fA * fB < 0)
	while (steps--) {
		const c = (b + a) / 2
		const fC = f(c)
		if (sign(fA) == sign(fC)) {
			a = c
			fA = fC
		} else {
			b = c
			fB = fC
		}
	}
	assert(a <= (b + a) / 2)
	assert(b >= (b + a) / 2)
	return lerp(a, b, 0.5)
}
export function newtonIterate(
	f: (x: number[]) => number[],
	x: number[],
	steps: int = 4,
	EPSILON?: number,
	stepize = 1,
) {
	EPSILON = EPSILON || 1e-8

	for (let i = 0; i < steps; i++) {
		const fx = f(x)
		const dfdx = Matrix.jacobi(f, x, fx, EPSILON)
		assert(!dfdx.isZero())
		const dx = dfdx.solveLinearSystem(new Vector(new Float64Array(fx))).v
		assert(!isNaN(dx[0]))
		//console.log('fx / dfdx', fx / dfdx)
		for (let j = 0; j < x.length; j++) x[j] -= dx[j] * stepize
	}
	return x
}

export function newtonIterate1d(
	f: (x: number) => number,
	xStart: number,
	steps: number | undefined,
	df: (x: number) => number,
): number
export function newtonIterate1d(f: (x: number) => number, xStart: number, steps: number, EPSILON?: number): number
export function newtonIterate1d(
	f: (x: number) => number,
	xStart: number,
	steps: number = 8,
	epsOrDf: number | ((x: number) => number) = 1e-8,
): number {
	if ('number' === typeof epsOrDf) {
		let x = xStart

		for (let i = 0; i < steps; i++) {
			const fx = f(x)
			const dfdx = (f(x + epsOrDf) - fx) / epsOrDf
			//console.log('fx / dfdx', fx / dfdx)
			x = x - fx / dfdx
		}
		return x
	} else {
		return newtonIterateWithDerivative(f, xStart, steps, epsOrDf)
	}
}
