export function newtonIterate2d(
	f1: (s: number, t: number) => number,
	f2: (s: number, t: number) => number,
	sStart: number,
	tStart: number,
	steps?: number,
): V3 | undefined {
	const EPSILON = 1e-6
	steps = steps || 4
	let s = sStart,
		t = tStart,
		f1ts,
		f2ts
	do {
		/*
         | a b |-1                   |  d -b |
         | c d |   = 1 / (ad - bc) * | -c  a |
         */
		f1ts = f1(s, t)
		f2ts = f2(s, t)
		/*
         let df1s = (f1(s + EPSILON, t) - f1ts) / EPSILON, df1t = (f1(s, t + EPSILON) - f1ts) / EPSILON,
         df2s = (f2(s + EPSILON, t) - f2ts) / EPSILON, df2t = (f2(s, t + EPSILON) - f2ts) / EPSILON
         let det = df1s * df2t - df1t * df2s
         s = s - ( df2t * f1ts - df1t * f2ts) / det
         t = t - (-df2s * f1ts + df1s * f2ts) / det
         */
		// TODO: is this even more accurate?
		const df1s = f1(s + EPSILON, t) - f1ts,
			df1t = f1(s, t + EPSILON) - f1ts,
			df2s = f2(s + EPSILON, t) - f2ts,
			df2t = f2(s, t + EPSILON) - f2ts
		const det = (df1s * df2t - df1t * df2s) / EPSILON
		const ds = (df2t * f1ts - df1t * f2ts) / det
		const dt = (-df2s * f1ts + df1s * f2ts) / det
		s -= ds
		t -= dt
	} while (--steps && f1ts ** 2 + f2ts ** 2 > NLA_PRECISION)
	if (f1ts ** 2 + f2ts ** 2 > NLA_PRECISION) {
		console.log(f1ts ** 2 + f2ts ** 2)
		return undefined
	}
	return new V3(s, t, 0)
}
export function newtonIterate2dWithDerivatives(
	f: (s: number, t: number) => number,
	g: (s: number, t: number) => number,
	sStart: number,
	tStart: number,
	steps: int,
	dfds: (s: number, t: number) => number,
	dfdt: (s: number, t: number) => number,
	dgds: (s: number, t: number) => number,
	dgdt: (s: number, t: number) => number,
): V3 | undefined {
	steps = steps || 4
	let s = sStart,
		t = tStart
	let f1ts, f2ts
	do {
		/*
         | a b |-1                   |  d -b |
         | c d |   = 1 / (ad - bc) * | -c  a |
         */
		f1ts = f(s, t)
		f2ts = g(s, t)
		const df1s = dfds(s, t),
			df1t = dfdt(s, t),
			df2s = dgds(s, t),
			df2t = dgdt(s, t)
		// TODO: is this even more accurate?
		const det = df1s * df2t - df1t * df2s
		const ds = (df2t * f1ts - df1t * f2ts) / det
		const dt = (-df2s * f1ts + df1s * f2ts) / det
		s -= ds
		t -= dt
	} while (--steps && f1ts * f1ts + f2ts * f2ts > NLA_PRECISION / 32)
	if (!steps) {
		//console.log(f1ts * f1ts + f2ts * f2ts)
		return undefined
	}
	return V(s, t, 0)
}

