export function gaussLegendreQuadrature24(f: (x: number) => number, startT: number, endT: number): number {
	//let result = 0
	//for (let i = 0; i < gaussLegendre24Xs.length; i++) {
	//	// gauss-legendre goes from -1 to 1, so we need to scale
	//	let t = startT + (gaussLegendre24Xs[i] + 1) / 2 * (endT - startT)
	//	result += gaussLegendre24Weights[i] * f(t)
	//}
	//const result = NLA
	//		.arrayFromFunction(24, i => startT + (gaussLegendre24Xs[i] + 1) / 2 * (endT - startT))
	//		.map((t, i) => gaussLegendre24Weights[i] * f(t))
	//		.sumInPlaceTree()
	//99.54182500782605
	//99.54182500782602
	// again, [-1,1], so div by 2
	//return result // 2 * (endT - startT)

	return (glq24_11(t => f(startT + ((t + 1) / 2) * (endT - startT))) / 2) * (endT - startT)
}

/**
 * Calculate the integral of f in the interval [-1;1].
 * @param f
 */
export function glq24_11(f: (x: number) => number): number {
	return arrayFromFunction(24, i => gaussLegendre24Weights[i] * f(gaussLegendre24Xs[i])).sumInPlaceTree()
}
export function glqInSteps(f: (x: number) => number, startT: number, endT: number, steps: int) {
	const dt = (endT - startT) / steps
	return (
		(arrayFromFunction(steps, i => glq24_11(t => f(startT + dt * i + ((t + 1) / 2) * dt))).sumInPlaceTree() / 2) *
		dt
	)
}

export function midpointRuleQuadrature(
	f: (x: number) => number,
	startT: number,
	endT: number,
	steps: int = 32,
): number {
	const dt = (endT - startT) / steps
	return (
		arrayFromFunction(steps, i => startT + dt / 2 + dt * i)
			.map(f)
			.sumInPlaceTree() * dt
	)
}

export function callsce(name: string, ...params: any[]) {
	return name + '(' + params.map(SCE).join(',') + ')'
}
